.tic-tac-toe {
    .heading {
        padding-top: 40px;
        padding-bottom: 20px;
        text-align: center;
        color: purple;
        font-family: monospace;
        font-size: 50px;
    }
    .buttonArea {
        justify-content: center;
        padding: 20px;
        margin-top: 10px;
    }
    .reset-btn {
        margin-left: 20px;
    }
    .gameArea {
        text-align: center;
    }
    .tableArea {
        margin-top: 30px;
        margin-bottom: 30px;
        display: flex;
        justify-content: center;
    }
    .description {
        padding: 20px;
        margin-bottom: 10px;
    }
    td {
        line-height: 0;
        padding: 0;
        width: 100px;
        height: 100px;
        font-size: 40px;
    }
    .boxButton {
        border: 0;
        width: 100%;
        height: 100%;
        background-color: #eee;
    }
    .endGameBtn {
        margin-bottom: 20px;
    }
}